<template>
  <div class="game-container">
    <!-- Игровая информация -->
    <div class="game-info">
      <div class="score-panel">
        <div class="score-item">Score: <span>{{ score }}</span>₿</div>
        <div class="score-item">Lives: <span>{{ lives }}</span></div>
        <div class="score-item">Time: <span>{{ timeLeft }}</span>s</div>
      </div>
    </div>

    <!-- Игровое поле -->
    <div class="game-field-wrap">
      <div class="game-field" ref="gameField" @click="handleClick">
        <!-- Кот -->
        <img 
          :src="catImage" 
          class="cat"
          :style="{
            transform: `translate(-50%, -50%) scale(${catScale})`
          }"
          alt="Cat"
        />
        
        <!-- Монеты -->
        <template v-for="coin in coins" :key="coin.id">
          <img 
            :src="coinImage"
            class="coin"
            :style="{
              left: (coin.x - coin.size/2) + 'px',
              top: (coin.y - coin.size/2) + 'px',
              width: coin.size + 'px',
              height: coin.size + 'px'
            }"
            alt="Coin"
          />
        </template>
      </div>
      <!-- Статус игры и кнопки -->
      <div class="game-status">
        <template v-if="gameOver">
          <template v-if="isFirstGame">
            <h2>Let's play and<br>PUMP the cat!</h2>
          </template>
          <template v-else>
            <h2 class="game-over-title">BITCAT PUMPED!</h2>
            <p>Your score: {{ score }}₿</p>
          </template>
          <button class="play-button" :disabled="isButtonDisabled" @click="startGameHandler">{{ isFirstGame ? 'Play' : 'Play Again' }}</button>
          <button v-if="gameOver && !isFirstGame" class="share-button" @click="showShare = true">Share</button>
          <button v-if="isFirstGame" class="rules-button" @click="showRules = true">Rules</button>
        </template>
      </div>
    </div>

    <div v-if="error" class="error-message">
      {{ error }}
    </div>

    <GameHistoryWidget />

    <RulesPopup v-model="showRules" />
    <SharePopup v-model="showShare" :score="score" />
  </div>
</template>

<script>
import { ref, onMounted, onUnmounted, computed, watch } from 'vue'
import GameHistoryWidget from '@/components/GameHistoryWidget.vue';
import store from '@/vuex'
import catImage from '@/assets/bitcat.webp'
import coinImage from '@/assets/coin.png'
import RulesPopup from '@/components/RulesPopup.vue'
import SharePopup from '@/components/SharePopup.vue'

export default {
  name: 'SoloGamePage',
  
  components: {
    RulesPopup,
    SharePopup,
    GameHistoryWidget
  },

  setup() {
    const gameField = ref(null)
    const showRules = ref(false)
    const showShare = ref(false)
    const isButtonDisabled = ref(false)

    // Computed properties using store state
    const score = computed(() => store.state.game.score)
    const lives = computed(() => store.state.game.lives)
    const timeLeft = computed(() => store.state.game.timeLeft)
    const coins = computed(() => store.state.game.coins)
    const isPlaying = computed(() => store.state.game.isPlaying)
    const gameOver = computed(() => store.state.game.gameOver)
    const isFirstGame = computed(() => store.state.game.isFirstGame)
    const catScale = computed(() => {
      console.log('Cat scale computed:', store.state.game.catScale)
      return store.state.game.catScale || 0.5
    })
    const error = computed(() => store.state.error)

    // Отслеживаем изменения размера кота
    watch(catScale, (newScale, oldScale) => {
      console.log('Cat scale changed:', { old: oldScale, new: newScale })
    })

    // Watch for game over to show share popup
    watch(gameOver, (newValue) => {
      if (newValue && !isFirstGame.value) {
        showShare.value = true
      }
    })

    // Game methods
    const initSocket = () => {
      store.dispatch('initSocket')
    }

    const startGameHandler = () => {
      isButtonDisabled.value = true
      setTimeout(() => {
        isButtonDisabled.value = false
      }, 5000)
      const rect = gameField.value.getBoundingClientRect()
      
      console.log('🎮 Start Game - Store State:', {
        isAuthenticated: store.getters.isAuthenticated,
        userTag: store.getters.userTag
      });

      if (!store.state.socket) {
        console.log('Initializing socket')
        initSocket()
      }

      // Добавляем небольшую задержку для уверенности, что сокет подключился
      setTimeout(() => {
        console.log('Dispatching startGame')
        store.dispatch('startGame', {
          playerId: store.getters.userTag || 'no_named_cat',
          width: rect.width,
          height: rect.height
        })
      }, 500)
    }

    const handleClick = (event) => {
      if (!isPlaying.value) return
      
      const field = gameField.value
      const rect = field.getBoundingClientRect()
      
      // Получаем масштаб
      const scaleX = field.offsetWidth / rect.width
      const scaleY = field.offsetHeight / rect.height
      
      // Получаем координаты клика относительно игрового поля с учетом масштаба
      const x = (event.clientX - rect.left) * scaleX
      const y = (event.clientY - rect.top) * scaleY
      
      console.log('Click coordinates:', { 
        x, 
        y,
        scale: { scaleX, scaleY },
        client: { 
          x: event.clientX, 
          y: event.clientY 
        },
        rect: {
          left: rect.left,
          top: rect.top,
          width: rect.width,
          height: rect.height
        }
      })
      
      store.dispatch('handleCoinClick', { x, y })
    }

    const cleanup = () => {
      store.dispatch('cleanup')
    }

    // Lifecycle hooks
    onMounted(() => {
      initSocket()
      // document.body.style.overflow = 'hidden'
    })

    onUnmounted(() => {
      cleanup()
      document.body.style.overflow = 'auto'
    })

    return {
      gameField,
      showRules,
      showShare,
      catImage,
      coinImage,
      score,
      lives,
      timeLeft,
      coins,
      isPlaying,
      gameOver,
      isFirstGame,
      catScale,
      error,
      handleClick,
      startGameHandler,
      isButtonDisabled
    }
  }
}
</script>

