<template>
  <div v-if="modelValue" class="share-popup-overlay" @click="$emit('update:modelValue', false)">
    <div class="share-popup" @click.stop>
      <div class="share-header">
        <h2>Share on X</h2>
        <button class="close-button" @click="$emit('update:modelValue', false)">×</button>
      </div>
      <div class="share-content">
        <div class="share-preview">
          <img src="@/assets/bitcat-animation.webp" alt="BITCAT" class="share-image" />
          <div class="share-text">
            <p>My BITCAT score: {{ score }}₿! 🎮</p>
            <p>Can you beat it? 🏆</p>
            <p>Play now at solanabitcat.com/game 🐱</p>
          </div>
        </div>
        <button class="share-button" @click="shareOnTwitter">
          Share on X
        </button>
      </div>
    </div>
  </div>
</template>

<script setup>
import { defineProps, defineEmits } from 'vue'

const props = defineProps({
  modelValue: Boolean,
  score: Number
})

const emit = defineEmits(['update:modelValue'])

const shareOnTwitter = () => {
  const text = `My $BITCAT score: ${props.score}₿! 🎮\nCan you beat it? 🏆\nPlay now at solanabitcat.com/game 🐱 @bitcat_sol_ #BITCAT_GAME #BITCAT`
  const url = 'https://solanabitcat.com/game/'
  const twitterUrl = `https://twitter.com/intent/tweet?text=${encodeURIComponent(text)}&url=${encodeURIComponent(url)}`
  window.open(twitterUrl, '_blank')
}
</script>

